import React from 'react';


const Card = (gino) =>{
	let oKGIF = true;
	let larghezza = 0;
	if((gino.prop.w).indexOf("%")===-1)
		larghezza = gino.prop.w
		else
			larghezza = (gino.prop.inWidth)*0.85;
	const lInk = gino.link;
	var pos = lInk.lastIndexOf("/");

	const lInkpart1 = lInk.slice(0, pos);
	const lInkpart2 = lInk.slice(pos, lInk.length);
	const newLink = lInkpart1 + "/ok" + lInkpart2;

	const lInkpart3 = lInkpart2.slice((lInkpart2.length-3), lInkpart2.length);
	if(lInkpart3==='gif')
		oKGIF= false;

	let lInkMOD = lInk;
		if(gino.prop.type!=="mobile"){
			//console.log("desk///////////////")

		}else if(oKGIF){
			//console.log("mobile///////////////")
			const lInkpart1Mobile = lInk.slice(0, pos);
			const lInkpart2Mobile = lInk.slice(pos, lInk.length);
			lInkMOD = lInkpart1Mobile + "/okMobile" + lInkpart2Mobile;

		}


		


	return (

		<div className="dib">

			<img

				id={`${gino.id}`}
				className=" br3 ma2 grow bw2 shadow-5 displayNone"
				height={gino.prop.h}
				width={gino.prop.w}
				alt={`${gino.id}`}
				src={`${lInkMOD}`}
				onClick={gino.click}
				onLoad={
					function(){
						let immagine = document.getElementById(`${gino.id}`);
						let immagineredux = document.getElementById(`${gino.id}ok`);
						immagine.classList.toggle("displayNone");
						immagine.classList.add("dib");
						immagineredux.classList.toggle("dib");
						immagineredux.classList.add("displayNone");


					}
				}
			/>
			{oKGIF
				? <img
					style={{filter: 'blur(2px)'}}
					id={`${gino.id}ok`}
					className=" dib br3 ma2 grow bw2 shadow-5"
					height={gino.prop.h}
					width={larghezza}
					alt={`${gino.id}ok`}
					src={newLink}
					onClick={gino.click}
					/>
				: <div
					id={`${gino.id}ok`}
					className=" dib br3 ma2 grow bw2 shadow-5"
					height={gino.prop.h}
					width={larghezza}
					alt={`${gino.id}ok`}>Caricamento gif</div>
			}

			</div>

		);
}

export default Card;



