import React from 'react';
import Card from './Card';



const CardList = (gino) => { 
	
	const arrayRobotsAsc = gino.robots;
	const arrayRobotsDesc = [];
	
	

	for (var i = arrayRobotsAsc.length - 1; i >= 0; i--) {
		arrayRobotsDesc[arrayRobotsAsc.length-1-i]=arrayRobotsAsc[i];
	}
	//console.log(arrayRobotsDesc);
	return(
		<div  id="galleria">
			{
				arrayRobotsDesc.map((user, i) => {
					return 	(<Card 
								click={gino.click}
								prop={gino.prop}
								link={arrayRobotsDesc[i].link}
								robots={arrayRobotsDesc}
								key={arrayRobotsDesc[i].id} 
								id={arrayRobotsDesc[i].id}
								name={arrayRobotsDesc[i].name} 
								email={arrayRobotsDesc[i].email}/>);})
			}
		</div>
		);
}

export default CardList;


